import { Link } from "react-scroll";

const HeroSection = () => {
  return (
    <div className="jumbotron jumbotron-fluid mt-5">
      <div className="container">
        <h1 className="display-4">Lotto Checker</h1>
        <p className="lead">
          Want to take your lotto checking to the next NEXT level?
        </p>
        <Link
          className="btn btn-outline-success btn-lg"
          to="services"
          smooth={true}
          duration={500}
          spy={true}
          exact="true"
          activeClass="active"
          offset={-80}
        >
          Try it Now!
        </Link>
      </div>
    </div>
  );
};

export default HeroSection;
