import "./page.css";

const About = () => {
  return (
    <div className="container mt-4" id="about">
      <h4 className="text-center">Check your lotto tickets instantly!</h4>
      <p className="text-center lead">
        This tool is powered by Optical Character Recognition.<br></br> Created
        with the purpose of helping Filipinos to reduce time on checking lotto
        tickets.
      </p>

      <div className="card-deck mb-3  text-center">
        <div className="col-lg">
          <div className="card mb-4 box-shadow ">
            <div className="card-header">
              <h4 className="my-0 font-weight-normal">Free</h4>
            </div>
            <div className="card-body">
              <p className="card-text p-3 lead">
                No absurd fees, but you have the option to donate for our
                services if you like!
              </p>
            </div>
          </div>
        </div>

        <div className="col-lg">
          <div className="card mb-4 box-shadow ">
            <div className="card-header">
              <h4 className="my-0 font-weight-normal">Simple</h4>
            </div>
            <div className="card-body">
              <p className="card-text lead p-3">
                No complex instructions, just take a picture and you're done!
              </p>
            </div>
          </div>
        </div>

        <div className="col-lg">
          <div className="card mb-4 box-shadow ">
            <div className="card-header">
              <h4 className="my-0 font-weight-normal">Efficient</h4>
            </div>
            <div className="card-body">
              <p className="card-text p-3 lead">
                We'll get you your winning numbers with an acceptable degree of
                accuracy!
              </p>
            </div>
          </div>
        </div>
      </div>
      <p className=" text-center text-muted">
        *We are not affiliated with the PCSO, this is merely a tool used for
        checking PCSO lotto tickets. You must be at least 18 years old to buy a
        lotto ticket or claim your winnings as stated by PCSO lotto rule.
        Lastly, we are NOT responsible for your actions.*
      </p>
    </div>
  );
};

export default About;
