import ReactCrop from "react-image-crop";
import "react-image-crop/dist/ReactCrop.css";
import React, { Component } from "react";
import Result from "./Result";
import How from "./How";
import "./page.css";

export default class Service extends Component {
  state = {
    src: null,
    crop: "",
    typeL: ["6-42", "6-45", "6-49", "6-55", "6-58"],
    getDate: null,
    typeVal: "6-42",
    croppedImageUrl: null,
    //curr64: null,
    currBlob: null,
    resultObj: null,
    alert: false,
    loading: false,
  };

  convertToBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        var base64String = reader.result.toString().replace(/^data:(.*,)?/, "");
        if (base64String.length % 4 > 0) {
          base64String += "=".repeat(4 - (base64String.length % 4));
        }
        resolve(base64String);
      };
      reader.onerror = (error) => {
        reject(error);
      };
    });
  };

  onSelectFile = (e) => {
    if (e.target.files && e.target.files.length > 0) {
      const reader = new FileReader();
      reader.addEventListener("load", () => {
        this.setState({ src: reader.result });
        this.setState({ currBlob: e.target.files[0] });
      });

      reader.readAsDataURL(e.target.files[0]);

      //reset things if new image
      this.setState({ croppedImageUrl: null });
      this.setState({ currBlob: null });
      this.setState({ crop: "" });
      this.setState({ resultObj: null });
    }
  };

  // If you setState the crop in here you should return false.
  onImageLoaded = (image) => {
    this.imageRef = image;
  };

  onCropComplete = (crop) => {
    //console.log(crop + "completed cropping");
    this.makeClientCrop(crop);
  };

  onCropChange = (crop) => {
    // You could also use percentCrop:
    // this.setState({ crop: percentCrop });
    this.setState({ crop });
  };

  formatDate = (e) => {
    //month/dd/yyyy

    var unconvertedDate = new Date(e);
    var dateArr = e.split("-");
    const monthsList = [
      "january",
      "february",
      "march",
      "april",
      "may",
      "june",
      "july",
      "august",
      "september",
      "october",
      "november",
      "december",
    ];
    var convertedMonth = monthsList[unconvertedDate.getMonth()];

    var newDate = `${convertedMonth}-${parseInt(dateArr[2])}-${dateArr[0]}`;

    this.setState({ getDate: newDate });
  };

  async makeClientCrop(crop) {
    if (this.imageRef && crop.width && crop.height) {
      const croppedImageUrl = await this.getCroppedImg(
        this.imageRef,
        crop,
        "newFile.jpeg"
      );
      this.setState({ croppedImageUrl });

      //this.convertCurrBlobToBase64();
      //console.log(croppedImageUrl + "i should change");
    }
  }

  triggerAlert = (val) => {
    this.setState({ alert: val });
    //console.log(this.state.alert);
  };

  onSubmitClicked = async (e) => {
    const { REACT_APP_API_URL } = process.env;
    if (this.state.getDate) {
      this.setState({ loading: true });
      e.preventDefault();
      this.triggerAlert(false);
      //console.log(this.state.currBlob + " curr blob");
      var val3 = await this.convertToBase64(this.state.currBlob);
      var obj = {
        test1: this.state.typeVal,
        test2: this.state.getDate,
        test3: val3,
      };
      fetch(REACT_APP_API_URL, {
        method: "post",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(obj),
      })
        .then((response) => response.json())
        .then((data) => this.getJsonObj(data))
        .catch((error) => {
          this.failedLoad(error);
        });
    } else this.triggerAlert(true);
  };

  failedLoad(error) {
    alert("Error the page took too long to load!\nPlease try again.\n" + error);
    this.setState({ loading: false });
  }

  getJsonObj(object) {
    this.setState({ resultObj: object, loading: false });

    //console.log(object);
  }

  getCroppedImg(image, crop, fileName) {
    const canvas = document.createElement("canvas");
    const scaleX = image.naturalWidth / image.width;
    const scaleY = image.naturalHeight / image.height;
    canvas.width = crop.width;
    canvas.height = crop.height;
    const ctx = canvas.getContext("2d");

    ctx.drawImage(
      image,
      crop.x * scaleX,
      crop.y * scaleY,
      crop.width * scaleX,
      crop.height * scaleY,
      0,
      0,
      crop.width,
      crop.height
    );

    return new Promise((resolve, reject) => {
      canvas.toBlob((blob) => {
        if (!blob) {
          //reject(new Error('Canvas is empty'));
          //console.error("Canvas is empty");
          return;
        }
        blob.name = fileName;
        window.URL.revokeObjectURL(this.fileUrl);

        this.fileUrl = window.URL.createObjectURL(blob);
        this.setState({ currBlob: blob });
        resolve(this.fileUrl);
      }, "image/jpeg");
    });
  }

  render() {
    const {
      crop,
      croppedImageUrl,
      src,
      typeL,
      resultObj,
      alert,
      loading,
    } = this.state;
    return (
      <div id="services">
        <How />
        <form className="container " autoComplete="off">
          <div className="form-row">
            <div className="form-group col-md-2">
              <label htmlFor="lotto-type" className="col-form-label">
                Lotto Type
              </label>
              <select
                id="lotto-type"
                className="form-control"
                onChange={(e) => {
                  this.setState({ typeVal: e.target.value });
                }}
              >
                {typeL.map((item) => (
                  <option key={item} value={item}>
                    {item}
                  </option>
                ))}
              </select>
            </div>

            <div className="form-group col-md-3">
              <label htmlFor="lotto-date" className="col-form-label">
                Draw Date
              </label>
              <input
                className="form-control"
                type="date"
                onChange={(e) => this.formatDate(e.target.value)}
                id="lotto-date"
              />
              {alert && (
                <div className="alert alert-danger alert-dismissible fade show">
                  <strong>Required!</strong> field must not be empty.
                  <button
                    type="button"
                    className="close"
                    aria-label="Close"
                    onClick={() => this.triggerAlert(false)}
                  >
                    <span aria-hidden="true">&times;</span>
                  </button>
                </div>
              )}
            </div>
          </div>
          <div className="form-row">
            <div className="form-group">
              <label className="h6" htmlFor="img-upload">
                <span className=" badge badge-info">Tip:</span> Upload image of
                lotto ticket
              </label>
              <input
                type="file"
                accept="image/*"
                onChange={(e) => this.onSelectFile(e)}
                className="form-control-file"
                id="img-upload"
              />
              {src && (
                <React.Fragment>
                  <ReactCrop
                    src={src}
                    crop={crop}
                    ruleOfThirds
                    onImageLoaded={this.onImageLoaded}
                    onComplete={this.onCropComplete}
                    onChange={this.onCropChange}
                  />
                  <p className="h6">
                    <span className=" badge badge-info">Tip:</span> Crop only
                    the section where the lotto numbers are found. And please
                    use clear image to make OCR accurate.
                  </p>

                  {croppedImageUrl && (
                    <img
                      className="row p-3"
                      alt="Crop"
                      style={{ maxWidth: "100%" }}
                      src={croppedImageUrl}
                    />
                  )}
                  <button
                    type="button"
                    className="btn btn-outline-primary mr-1 col-md-2"
                    onClick={(e) => this.onSubmitClicked(e)}
                  >
                    Analyze
                  </button>
                </React.Fragment>
              )}
            </div>
          </div>
        </form>
        {loading ? (
          <div className="container d-flex justify-content-center spin mt-4 mb-4"></div>
        ) : null}
        {resultObj && <Result resJson={resultObj} />}
      </div>
    );
  }
}
