import ReactPlayer from "react-player";

const How = () => {
  return (
    <div className="container mb-4" style={{ marginTop: "20px" }} id="services">
      <h4 className="text-center">How to Use</h4>
      <p className="text-center lead">
        Follow this quick tutorial below to learn more.
      </p>
      <div style={{ position: "relative", paddingTop: "40%" }}>
        <ReactPlayer
          controls
          style={{ position: "absolute", top: "0", left: "0" }}
          url="https://youtu.be/8YW_xtmHiCI"
          width="100%"
          height="100%"
        />
      </div>
    </div>
  );
};

export default How;
