import React, { useState, useEffect } from "react";

const Result = ({ resJson }) => {
  const [newwin, setWin] = useState(null);

  useEffect(() => {
    document.getElementById("user-nums-ocr").value = convertToText(resJson.ocr);
    setWin(convertToText(resJson.yourwin));
  }, [resJson.ocr, resJson.yourwin]);

  const FailedCheckWin = () => {
    return (
      <React.Fragment>
        <p className="h6">
          <span className=" badge badge-danger">Error:</span> Could not find
          winning numbers for lotto {resJson.type} with date {resJson.date}.
          Please try changing lotto type or the draw date.
        </p>
      </React.Fragment>
    );
  };

  const CheckWinTrue = () => {
    return (
      <p>
        <span className="h6 pt-1">Winning Numbers:</span> {resJson.win} (In any
        order)
      </p>
    );
  };

  const convertToText = (resObj) => {
    var txt = "";
    resObj.forEach((element) => {
      if (element.length === 0) txt += "None";
      element.forEach((nxtElem, i) => {
        if (i + 1 === element.length) txt += nxtElem.toString();
        else txt += nxtElem.toString() + "-";
      });
      txt += "\n";
    });
    //console.log(txt);
    return txt;
  };

  const RecheckNumbers = (yournum, win) => {
    var row = yournum.split("\n");
    var winNum = win.split("-").map(Number);
    var txt = [];
    row.forEach((rowVal) => {
      var rowInt = rowVal.split("-").map(Number);
      var temp = [];
      rowInt.some((arr) => {
        if (winNum.includes(arr)) {
          temp.push(arr);
        }
      });
      txt.push(temp);
    });
    // console.log(txt);
    // console.log(win);
    setWin(convertToText(txt));
  };

  return (
    <form className="container">
      {resJson.win === "404" ? <FailedCheckWin /> : <CheckWinTrue />}
      <div className="row">
        <p className="col-md ">
          <span className="h6">Lotto Type:</span> {resJson.type}
        </p>
        <p className="col-md">
          <span className="h6">Draw Date:</span> {resJson.date}
        </p>
      </div>
      <div className="form-row ">
        <div className="form-group col-md">
          <h6>Your Numbers</h6>
          <textarea
            className="form-control"
            id="user-nums-ocr"
            rows="6"
            cols="50"
          ></textarea>
        </div>
        <div className="form-group col-md">
          <h6>Your Winning Numbers</h6>
          <textarea
            readOnly
            className="form-control"
            id="user-nums-correct"
            rows="6"
            cols="50"
            value={newwin ? newwin : convertToText(resJson.yourwin)}
          ></textarea>
        </div>
      </div>
      <p className="h6">
        <span className=" badge badge-warning">Notice:</span> If there are any
        incorrect numbers identified, please manually input your lotto numbers
        seperated by (-) in the text box provided and click the Recheck button
        or by changing the crop of the image and click the Analyze button.
      </p>

      <button
        type="button"
        className="btn btn-outline-primary mr-1 col-md-2 mb-3 mt-2"
        onClick={() =>
          RecheckNumbers(
            document.getElementById("user-nums-ocr").value.trim(),
            resJson.win
          )
        }
      >
        Recheck
      </button>
    </form>
  );
};

export default Result;
