import React, { useState } from "react";
import { List, X } from "react-bootstrap-icons";
import { Link, animateScroll as scroll } from "react-scroll";

const Navigation = () => {
  const [menuToggled, setToggled] = useState(true);

  return (
    <nav className={" navbar fixed-top navbar-expand-lg navbar-dark bg-dark"}>
      <div className="container-fluid">
        <a
          className="navbar-brand soc"
          style={{ fontSize: "1.5rem" }}
          onClick={scroll.scrollToTop}
        >
          LCR
        </a>
        <button
          className="navbar-toggler float-right"
          type="button"
          onClick={() => setToggled(!menuToggled)}
        >
          {menuToggled ? (
            <List color="white" size={25} />
          ) : (
            <X color="white" size={25} />
          )}
        </button>
        <div
          className={(menuToggled ? "" : "show") + " collapse navbar-collapse"}
        >
          <ul className="navbar-nav  ">
            <li>
              <Link
                className="nav-link  "
                to="about"
                smooth={true}
                duration={500}
                spy={true}
                exact="true"
                activeClass="active"
                offset={-90}
              >
                About
              </Link>
            </li>
            <li className="nav-item">
              <Link
                className="nav-link "
                to="services"
                smooth={true}
                duration={500}
                spy={true}
                exact="true"
                activeClass="active"
                offset={-80}
              >
                Checker
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default Navigation;
